<template>
    <div>

        <van-nav-bar title="人才数据库" left-text="返回" left-arrow @click-left="back">
            <van-icon name="search" slot="right"/>
        </van-nav-bar>

        <van-search placeholder="请输入待检索对象姓名" v-model="value"/>

        <van-cell-group v-for="(author,i) in authors" @click="method_1(i)">
            <van-card :desc="author.desc" :title="author.name" thumb="docs/man_scientist_128px.png">
                <div slot="price">{{author.a10.toFixed(2)}} 分</div>
                <div slot="tags">
                    <van-tag plain type="danger" class="ttaagg" v-show="author.gyx">影响力人才</van-tag>
                    <van-tag plain type="primary" class="ttaagg" v-show="author.gcc">高产出人才</van-tag>
                    <van-tag plain type="success" class="ttaagg" v-show="author.gby">高被引人才</van-tag>
                </div>
            </van-card>
        </van-cell-group>

        <!--<van-cell-group @click="person">-->
        <!--<van-card desc="专利个数2,H指数:1,总被引次数:1" title="刘海峰" thumb="docs/man_scientist_128px.png">-->
        <!--<div slot="price">87.65分</div>-->
        <!--<div slot="tags">-->
        <!--<van-tag plain type="primary" class="ttaagg">创新潜力</van-tag>-->
        <!--</div>-->
        <!--</van-card>-->
        <!--</van-cell-group>-->

        <van-pagination
                v-model="currentPage"
                :page-count="totpage"
                mode="simple"
                @change="onSubmit"
        />
    </div>
</template>

<script>
    import {
        Button,
        CheckboxGroup,
        Card,
        SubmitBar,
        Toast,
        NavBar,
        Tag,
        Pagination,
        Search,
        Cell,
        CellGroup
    } from 'vant';
    import axios from 'axios';

    export default {
        components: {
            [Card.name]: Card,
            [Button.name]: Button,
            [SubmitBar.name]: SubmitBar,
            [CheckboxGroup.name]: CheckboxGroup,
            [NavBar.name]: NavBar,
            [Tag.name]: Tag,
            [Pagination.name]: Pagination,
            [Search.name]: Search,
            [Toast.name]: Toast,
            [Cell.name]: Cell,
            [CellGroup.name]: CellGroup,
        },

        data() {
            return {
                currentPage: 1,
                totpage: 1,
            };
        },

        computed: {},

        methods: {
            method_1(i) {
                // console.log(ev) // this is the event
                // console.log(i) // i is index of v-for
                this.$router.push('person/' + this.authors[i].tags);
            },


            back() {
                this.$router.go(-1);
            },
            init() {
                this.onSubmit();
            },
            onSubmit() {
                const path = `api/rencaishujuku/` + this.currentPage
                axios.get(path)
                    .then(response => {
                            this.totpage = response.data.totpage
                            this.currentPage = response.data.curpage
                            this.authors = response.data.authors


                            // 话
                            for (var i = 0; i < this.authors.length; i++) {
                                this.authors[i]['desc'] = "专利个数:" + this.authors[i]['a0'] + ','
                                    + "专利H指数:" + this.authors[i]['a6'] + ',' +
                                    "总被引次数:" + this.authors[i]['a1']


                                if (this.authors[i].a0 >= 7) {
                                    this.authors[i]['gcc'] = true
                                } else {
                                    this.authors[i]['gcc'] = false
                                }

                                if (this.authors[i].a1 >= 2) {
                                    this.authors[i]['gby'] = true
                                } else {
                                    this.authors[i]['gby'] = false
                                }

                                if (this.authors[i].a6 >= 2) {
                                    this.authors[i]['gyx'] = true
                                } else {
                                    this.authors[i]['gyx'] = false
                                }

                            }

                            // console.log(response.data)
                            this.$forceUpdate()
                        }
                    )
                    .catch(error => {
                        console.log(error)
                    })
            }


        },
        created() {
            this.init()
        }
    }
    ;
</script>
<style lang="less">

    .ttaagg {
        margin-left: 5px;
    }
</style>